<template>
    <form 
        @submit.prevent="submitForm">
        <input 
            v-model="title"
            type="text" 
            placeholder="Add another list"
        >
    </form>
</template>

<script>
import { ref } from "vue";

export default {
    setup ( props, { emit } ) {
        const title = ref('')

        const submitForm = () => {
            emit('new-list-coming', title.value)
            title.value = "";
        }

        return {
            title,
            submitForm
        }
    }
}
</script>

<style lang="scss">
    form {
        text-align: left;
        margin-bottom: 20px;
        input{
            border: 1px solid #001485;
            padding: 5px;
            border-radius: 5px;
            box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px, rgba(0, 0, 0, 0.22) 0px 10px 10px;
        }
    }
    
</style>