<template>
    <li 
        :class="{ 'z-20 ': isPopped }" 
        class="card"
        @contextmenu.prevent="doPop"
    >
        <CardLabels
                :labels="card.labels"
        />
        <CardImages 
            :image="card.image"
        />
        <div>
            <p
                ref="cardText"
                @keydown.enter="saveText"
                :contenteditable="isEditing"
                :class="{ 'shadow-outline': isEditing }"
            > 
                {{ card.text }}
            </p>

            <CardTags 
                :tags="card.tags"
            />

            

        </div>
            <transition name="pop">
                <CardPopUp 
                    v-if="isPopped"
                    @close-popup="undoPop"
                    @start-edit="startEditing"
                    :card="card"
                    :listId="listId"
                />
            </transition>
    </li>
</template>

<script>
import { ref, toRefs, reactive } from 'vue';

import CardTags from '@/components/CardTags.vue';
import CardLabels from '@/components/CardLabels.vue';
import CardImages from '@/components/CardImages.vue';
import CardPopUp from '@/components/CardPopUp.vue';

export default {
    components: {
        CardTags,
        CardLabels,
        CardImages,
        CardPopUp
    },
    props: {
        card: {
            type: Object,
        },
        listId: {
            type: Number,
        },
    },
    setup (props) {

        const cardText = ref(null)

        const state = reactive({
            isPopped: false,
            isEditing: false
        })

        const doPop = () => {
            state.isPopped = true
            window.eventBus.emit('toggle-overlay', true)
        }

        const undoPop = () => {
            state.isPopped = false
            window.eventBus.emit('toggle-overlay', false)
        }

        const startEditing = () => {
            state.isEditing = true
            // hack for contenteditable 
            setTimeout(() => cardText.value.focus(), 0 )
        }

        const saveText = () => {
            state.isEditing = false;

            window.eventBus.emit('edit-card-text', {
                cardId: props.card.id,
                listId: props.listId,
                newText: cardText.value.textContent
            })

            undoPop()
        }

        return {
            ...toRefs(state),
            doPop,
            undoPop,
            saveText,
            cardText,
            startEditing
        }
    }
}
</script>

<style lang="scss">
p {
    margin: 0;
}
.z-20 {
    z-index: 20;
}
.card {
    padding: 5px;
    background: #32385d;
    margin: 10px 0;
    border-radius: 5px;
    position: relative;
    footer{
        margin-top: 10px;
        display: flex;
        flex-wrap: wrap;
        a {
            padding: 2px;
            margin: 5px;
            background: rgb(247, 181, 181);
            color: #001485;
        }
    } 
    figure {
        margin: 0;
        img {
            width: 100%;
        }
    }
    aside {
        display: flex;
        padding-bottom: 10px;
        span {
            width: 20px;
            margin-right: 5px;
            border-radius: 3px;
            height: 7px;
            &.bg-yellow {
                background-color: rgb(255, 255, 100);
            }
            &.bg-green {
                background-color: rgb(141, 253, 141);
            }
            &.bg-red {
                background-color: rgb(255, 110, 110);
            }
            &.bg-black {
                background-color: rgb(41, 41, 41);
            }
            &.bg-blue {
                background-color: rgb(132, 152, 253);
            }
        }
    } 
}

.pop-enter-active,
.pop-leave-active {
    transition: all .1s ease-in;
}
.pop-enter-from, 
.pop-leave-to {
    opacity: 0;
    transform: translateX(-25%);
}
</style>