<template>
    <ul>
        <li
            @click="emit('start-edit')"
        >
            Edit text
        </li>
        <li
            @click="deleteCard"
        >
            trash
        </li>
        <li
            @click="emit('close-popup')"
        >
            X
        </li>
    </ul>
</template>

<script>

export default {
    props: {
        card: {
            type: Object
        },
        listId: {
            type: Number
        },
    },
    setup (props, {emit}) {

        let deleteCard = () => {
            window.eventBus.emit('delete-card', {
                cardId: props.card.id,
                listId: props.listId
            })

            //hide overlay
             window.eventBus.emit('toggle-overlay', false)
        }


        return {
            emit,
            deleteCard
        }
    }
}
</script>

<style lang="scss" scoped>
ul {
    position: absolute;
    right: -80px;
    top: 0;
    text-align: left;
    li {
        padding: 4px;
        margin: 3px;
        background: rgb(81, 81, 81);
        color: white;
    }
}
</style>