<template>
    <section>
        <div class="inner-wrapper">
            <header>
                <h3 
                    ref="titleElement"
                    @click="startEditing"
                    @blur="saveTitle"
                    @keydown.enter="saveTitle"
                    :contenteditable="isEditing"
                    :class="{ 'shadow-outline': isEditing }"
                > 
                    {{ title }} 
                </h3>
            </header>
            <transition-group name="card" tag="ul">
                <Card v-for="card in cards" :card="card" :listId="id" :key="card.id" />
            </transition-group>

            <CardCreateForm :listId='id' />
        </div>
    </section>
</template>

<script>
import { ref, reactive, toRefs } from 'vue'

import Card from '@/components/Card.vue'
import CardCreateForm from '@/components/CardCreateForm.vue'

export default {
    components: {
        Card, 
        CardCreateForm,
    },
    props: {
        id: {
            type: Number,
        },
        title: {
            type: String,
        },
        cards: {
            type: Array,
        }
    },
    setup ( props, {emit} ) {

        const titleElement = ref(null)

        const state = reactive({
            isEditing: false
        })

        const saveTitle = () => {
            state.isEditing = false;
            emit('edit-list-title', {
                id: props.id,
                newTitle: titleElement.value.textContent
            })
        }

        const startEditing = () => {
            state.isEditing = true
            // hack for contenteditable 
            setTimeout(() => titleElement.value.focus(), 0 )
        }

        return {
            ...toRefs ( state ),
            saveTitle,
            titleElement,
            startEditing
            
        }
    }
}
</script>

<style lang="scss">
section {
    border-radius: 15px;
    height: fit-content;
    flex-basis: 100%;
    @media (min-width: 576px) {
        flex-basis: calc(50% - 15px);
    }
    @media (min-width: 768px) {
        flex-basis: calc(25% - 15px);
    }
    @media (min-width: 992px) {
        flex-basis: calc(20% - 15px);
    }
    background-color: #b64242;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px, rgba(0, 0, 0, 0.22) 0px 10px 10px;
    ul {
        padding: 0px;
    }
    li {
        list-style-type: none;
    }
    footer a {
        color: white;
    }
    header {
        h3 {
            margin: 0;
        }
    }
    .inner-wrapper {
        margin: 10px;
    }
}
.shadow-outline {
    border: 1px solid rgb(211, 67, 67);
    outline: none;
    color: rgb(198, 9, 9);
    background: white;
}
.card-enter-active,
.card-leave-active {
    transition: all .15s;
}
.card-enter-from, 
.card-leave-to {
    opacity: 0;
    transform: scale(0.75);
}


</style>