<template>
    <footer>
        <a 
            v-if="linkVisible"
            @click="showForm" 
            href="#"
        > 
            Add card 
        </a>
    
        <form v-if="formVisible" @submit.prevent="submitForm" action="/">
            <textarea 
                v-model="text"
                ref="textAreaFocus">
            </textarea>
            <div>
                <button>Save</button>
                <a href="#" @click="hideForm">
                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 95.939 95.939" width="20px">
                    <path d="M62.819 47.97l32.533-32.534a2 2 0 000-2.828L83.333.586a2.002 2.002 0 00-2.828 0L47.97 33.121 15.435.586c-.75-.75-2.078-.75-2.828 0L.587 12.608a2 2 0 000 2.828L33.121 47.97.587 80.504a2 2 0 000 2.828l12.02 12.021a2 2 0 002.828 0L47.97 62.818l32.535 32.535a2 2 0 002.828 0l12.02-12.021a2 2 0 000-2.828L62.819 47.97z"/>
                    </svg>
                </a>
            </div>
        </form>
    </footer>
</template>

<script>
import { reactive, toRefs, nextTick, ref } from 'vue'

export default {
    props: {
        listId: {
            type: Number,
        }
    },
    setup ( props ) {

        // state
        const state = reactive({
            text: '',
            formVisible: false,
            linkVisible: true,
        })

        // secect text area
        const textAreaFocus = ref(null)

        // show form
        const showForm = () => {
            state.formVisible = true
            state.linkVisible = false
            nextTick(function() {
                textAreaFocus.value.focus()
            })
        }

        // submit form
        const submitForm = () => {
            window.eventBus.emit('new-card-created', {
                listId: props.listId,
                text: state.text
            })
            state.text="",
            textAreaFocus.value.focus()
        }

        // hide form
        const hideForm = () => {
            state.formVisible = false
            state.linkVisible = true
        }

        return {
            ...toRefs(state),
            showForm,
            hideForm,
            textAreaFocus,
            submitForm
        }
    }
}
</script>
