<template>
    <main>  
        <h1>To do list</h1>
        <ul class="description">
            <li>we can add another lists and its items</li>
            <li>we can edit headline of created list by doubleclicked it</li>
            <li>we can edit/delete content of list by right click on specific item</li>
        </ul>

        <ListCreateForm
            @new-list-coming="addNewList($event, lists)"
            :key="0"
        />
          
        <transition-group name="list" tag="div" class="wrapper">

            <div
                v-if="overlay"
                id="overlay"
                key="-1"
            >
            </div>

            <TruloList 
                v-for="list in lists" 
                :id="list.id" 
                :title="list.title" 
                :cards="list.cards" 
                :key="list.id"
                @edit-list-title="editListTitle($event, lists)"
            />

            

        </transition-group>
    </main>
</template>

<script>
// import
import { ref, onMounted } from 'vue'
import { data } from '@/data.js'

// functions
import { addNewCard, deleteCard, editCardText } from '@/cards.js';
import { addNewList, editListTitle } from '@/lists.js';

// @ is an alias to /src
import TruloList from '@/components/List.vue'
import ListCreateForm from '@/components/ListCreateForm.vue'

export default {
    components: {
        TruloList,
        ListCreateForm
    },
    setup () {
        const lists = ref(data)
        const overlay = ref(false)

        // events
        onMounted(() => {
            window.eventBus.on('new-card-created', event => {
                addNewCard(event, lists.value)
            })

            window.eventBus.on('delete-card', event => {
                deleteCard(event, lists.value)
            })

             window.eventBus.on('edit-card-text', event => {
                editCardText(event, lists.value)
            })

            window.eventBus.on('toggle-overlay', event => {
                overlay.value = event
            })
        })

        return { 
            lists,
            addNewList,
            editListTitle,
            overlay 
        }
    }
}
</script>

<style lang="scss">
.wrapper {
    display: inline-flex;
    flex-wrap: wrap;
    gap: 20px 15px;
}
.list-enter-active,
.list-leave-active {
    transition: all .15s;
}
.list-enter-from, 
.list-leave-to {
    opacity: 0;
    transform: scale(0.75);
}
#overlay {
    background-color:rgba(0, 0, 0, 0.5);
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: 10;
    transform: scale(1)
}
h1 {
    color: #001485;
}
.description {
    list-style: none;
    padding: 0;
}
main {
    padding: 0 20px 50px 20px;
}
</style>